import { useState, useEffect } from "react";

// react-router components
import { Routes, Route, Navigate, useLocation } from "react-router-dom";
import Home from "./layouts/Home";

// react-redux
import { useDispatch } from "react-redux";
import { useSelector } from "react-redux";

// @mui material components
import { ThemeProvider } from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";
import Icon from "@mui/material/Icon";

// Material Dashboard 2 React components
import MDBox from "components/MDBox";

// Material Dashboard 2 React example components
import Sidenav from "examples/Sidenav";
import Configurator from "examples/Configurator";

// Material Dashboard 2 React themes
import theme from "assets/theme";

// Material Dashboard 2 React Dark Mode themes
import themeDark from "assets/theme-dark";

// Material Dashboard 2 React routes
import routes from "routes";

// Material Dashboard 2 React contexts
import {
  useMaterialUIController,
  setMiniSidenav,
  setOpenConfigurator,
} from "context";

// Images
import brandWhite from "assets/images/logo.png";
import brandDark from "assets/images/logo.png";

// Store
import { authActions } from "./store/auth-slice";
import { getAuth, onAuthStateChanged } from "firebase/auth";
import Page404 from "layouts/Page404";
import LoadingPage from "layouts/LoadingPage";

import SignIn from "layouts/authentication/sign-in";
import SignUp from "layouts/authentication/sign-up";
import Instructions from "layouts/instructions";
import ResponsiveAppBar from"layouts/ResponsiveAppBar"

import Exam from "layouts/exam";
import Results from "layouts/results";
import EditExam from "layouts/exams/editExam";
import Cover from "layouts/authentication/reset-password/cover";
import Page from "layouts/content"
import ContactUs from "components/Contact-us/ContactUs";
import About from "components/about/about";

const auth = getAuth();

export default function App() {
  const [controller, dispatch] = useMaterialUIController();
  const {
    miniSidenav,
    direction,
    layout,
    openConfigurator,
    sidenavColor,
    transparentSidenav,
    whiteSidenav,
    darkMode,
  } = controller;
  const [onMouseEnter, setOnMouseEnter] = useState(false);
  const { pathname } = useLocation();

  // Open sidenav when mouse enter on mini sidenav
  const handleOnMouseEnter = () => {
    if (miniSidenav && !onMouseEnter) {
      setMiniSidenav(dispatch, false);
      setOnMouseEnter(true);
    }
  };

  // Close sidenav when mouse leave mini sidenav
  const handleOnMouseLeave = () => {
    if (onMouseEnter) {
      setMiniSidenav(dispatch, true);
      setOnMouseEnter(false);
    }
  };

  // Change the openConfigurator state
  const handleConfiguratorOpen = () =>
    setOpenConfigurator(dispatch, !openConfigurator);

  // Setting the dir attribute for the body element
  useEffect(() => {
    document.body.setAttribute("dir", direction);
  }, [direction]);

  // Setting page scroll to 0 when changing the route
  useEffect(() => {
    document.documentElement.scrollTop = 0;
    document.scrollingElement.scrollTop = 0;
  }, [pathname]);

  const dispatchActions = useDispatch();
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (user) => {
      if (user) {
    

        await dispatchActions(
          authActions.setCurrentUser({
            name: user.displayName,
            email: user.email,
            phone: user.phoneNumber,
            id: user.uid,
            idToken: user.accessToken,
            refreshToken: user.refreshToken,
          })
        );
      }
      setIsLoading(false);
    });

    return unsubscribe;
  }, [dispatchActions]);

  const [url, seturl] = useState(
    {
  
        url: '',
    }
  );
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);

  useEffect(() => {
    fetch(`${process.env.REACT_APP_BASE_URL}/api/v1/pages/logo`)
      .then(res => res.json())
      .then(
        (result) => {
          setIsLoaded(true);
       
          seturl({...url, link: `${process.env.REACT_APP_IMAGE_CLOUD}/public/uploads/${result.logo.src}`});
        
        },
     
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      )
  }, []);


  const getRoutes = (allRoutes) =>
    allRoutes.map((route) => {
      if (route.collapse) {
        return getRoutes(route.collapse);
      }

      if (route.route) {
        return (
          <Route
            exact
            path={route.route}
            element={route.component}
            key={route.key}
          />
        );
      }

      return null;
    });

  const configsButton = (
    <MDBox
      display="flex"
      justifyContent="center"
      alignItems="center"
      width="3.25rem"
      height="3.25rem"
      bgColor="white"
      shadow="sm"
      borderRadius="50%"
      position="fixed"
      right="2rem"
      bottom="2rem"
      zIndex={99}
      color="dark"
      sx={{ cursor: "pointer" }}
      onClick={handleConfiguratorOpen}
    >
      <Icon fontSize="small" color="inherit">
        settings
      </Icon>
    </MDBox>
  );

  const authStatus = useSelector((state) => state.auth);

  return (
    <ThemeProvider theme={darkMode ? themeDark : theme}>
      <CssBaseline />
      {isLoading && <LoadingPage />}
      {!isLoading && (
        <Routes>
          {!authStatus.currentUser && (
            <>
              <Route path="/" exact element={<Navigate to="/sign-in" />} />
              <Route path="/sign-in" element={<SignIn />} />
              <Route path="/sign-up" element={<SignUp />} />
              <Route path="/forgot-password" element={<Cover />} />
              <Route path="*" element={<Navigate to="/sign-in" />} />
              <Route path="/home" element={<Home />}  />
              <Route path="/content/:id" element={<Page />} />
              <Route path="/contactUs" element={<ContactUs />} />
              
            </>
          )}
          {authStatus.currentUser && (
            <>
             <Route path="/home" element={<Home />}  />
             <Route path="/contactUs" element={<ContactUs />} />
              <Route exact path="/" element={<Navigate to="/dashboard" />} />
              <Route path="/sign-in" element={<Navigate to="/dashboard" />} />
             <Route path='/about' element={<About />} />
              <Route
                path="/instructions/begin/:id"
                element={<Instructions />}
              />
              <Route path="/exams/begin/:id" element={<Exam />} />

              <Route path="/exams/view/:id" element={<EditExam />} />

              <Route path="/results/:id" element={<Results />} />
              <Route path="/content/:id" element={<Page />} />

              {getRoutes(routes)}
            </>
          )}
          <Route path="*" element={<Page404 />} />
        </Routes>
      )}
      {!isLoading && layout === "dashboard" && (
        <>
          <Sidenav
            color={sidenavColor}
            brand={
              url.link
            }
           
            routes={routes}
            onMouseEnter={handleOnMouseEnter}
            onMouseLeave={handleOnMouseLeave}
          />
          <Configurator />
          {configsButton}
        </>
      )}
      {layout === "vr" && <Configurator />}
    </ThemeProvider>
  );
}
